/* Default to Dark Mode */
body {
  background: #15110B;
  color: #D5D1CB;
  transition: background 0.3s, color 0.3s;
}

/* Light Mode (Applied Only When Active) */
body.light-mode {
  background: rgb(227, 223, 212);
  color: rgb(67, 74, 43);
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background: #15110B;
  border-bottom: 2px solid white;
}

/* Light Mode Navbar */
body.light-mode .navbar {
  background: rgb(227, 223, 212);
  border-bottom: 2px solid rgb(67, 74, 43);
}

/* Navbar Links */
.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-links a {
  text-decoration: none;
  color: #D5D1CB;
  font-weight: bold;
  font-size: 22px;
}

body.light-mode .nav-links a {
  color: rgb(67, 74, 43);
}

/* Button Styling */
.dark-mode-btn {
  background: none;
  border: 2px solid #D5D1CB;
  color: #D5D1CB;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s;
}

.dark-mode-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

body.light-mode .dark-mode-btn {
  border-color: rgb(67, 74, 43);
  color: rgb(67, 74, 43);
}

body.light-mode .dark-mode-btn:hover {
  background: rgba(0, 0, 0, 0.1);
}

/* Logo */
.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  height: 100px;
  width: auto;
}

/* Mobile Menu (Hidden by Default) */
.menu-icon {
  display: none;
  font-size: 28px;
  cursor: pointer;
  color: #D5D1CB;
}

body.light-mode .menu-icon {
  color: rgb(67, 74, 43);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .nav-links {
      display: none;
      flex-direction: column;
      background: #15110B;
      position: absolute;
      top: 60px;
      right: 0;
      width: 200px;
      text-align: right;
      padding: 10px;
      border-radius: 5px;
  }

  body.light-mode .nav-links {
      background: rgb(227, 223, 212);
  }

  .nav-links.active {
      display: flex;
  }

  .menu-icon {
      display: block;
  }
}
