.home-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 columns */
    gap: 20px; /* Space between blocks */
    padding: 20px;
}
  
/* General Block Styling */
.block {
    background: #15110B; /* Dark background */
    color: #D5D1CB;
    padding: 15px;
    text-align: center;
    font-size: 20px;
    border-radius: 10px;
    border: 2px solid whitesmoke;
    cursor: pointer; /* Pointer for better UX */
    position: relative; /* Needed for dropdown positioning */
}

/* ✅ Light Mode Styles */
body.light-mode .block {
    background: rgb(227, 223, 212); /* Light mode background */
    color: rgb(67, 74, 43); /* Text color */
    border: 2px solid black; /* Black border */
}

/* Responsive: 2 columns on tablets */
@media (max-width: 900px) {
    .home-container {
        grid-template-columns: repeat(2, 1fr);
    }
}

/* Responsive: 1 column on mobile */
@media (max-width: 600px) {
    .home-container {
        grid-template-columns: repeat(1, 1fr);
    }
}

/* ✅ Center & Enlarge Block 7 on Desktop */
@media (min-width: 901px) {
    #block7 {
        grid-column: 1 / span 3; /* Full width across all 3 columns */
        justify-self: center;
        width: 50%;
        padding: 35px;
        font-size: 22px;
    }
}

/* ✅ Light Mode for Block 7 */
body.light-mode #block7 {
    background: rgb(227, 223, 212);
    color: rgb(67, 74, 43);
    border: 2px solid black;
}

/* ✅ Center & Enlarge Block 7 on Tablet */
@media (max-width: 900px) and (min-width: 601px) {
    #block7 {
        grid-column: 1 / span 2; /* Span across both columns */
        justify-self: center;
        width: 100%;
        padding: 50px;
        font-size: 20px;
    }
}

/* ✅ Center Block 7 on Mobile */
@media (max-width: 600px) {
    #block7 {
        grid-column: auto; /* Default single-column behavior */
        width: 100%;
        padding: 40px;
        font-size: 18px;
    }
}

/* ✅ Dropdown Styling */
.dropdown {
    background: #15110B;
    color: #D5D1CB;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 10px;
    position: absolute;
    width: 85%; /* Match block width */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 5px;
    z-index: 100;
    max-height: 200px; /* Prevent dropdown overflow */
    overflow-y: auto; /* Allow scrolling if too many options */
}
  







.dropdown-item {
    padding: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background 0.3s;
    font-family: inherit; /* Match block font */
}

.dropdown-item:hover {
    background: #3D3933;
}


.results-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Flexible grid */
    gap: 20px;
    padding: 20px;
    justify-items: center; /* Center the items horizontally */
}

.result-card {
    width: 100%;
    max-width: 400px; /* Adjust card width */
    padding: 20px;
    border-radius: 8px;
    background: #1c1a15;
    color: #D5D1CB;
    text-align: left;
    box-sizing: border-box;
}

/* Light Mode */
body.light-mode .result-card {
    background: rgb(240, 240, 230);
    color: rgb(50, 50, 50);
    border: 1px solid black;
}
