.partners-container {
    padding: 2rem;
    
    text-align: center;
    /* color: white; */
  }
  
  .partners-heading {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .partners-description {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
  
  .partners-grid {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .partner-card {
    /* background-color: #1a1a1a; */
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0px 4px 6px black;
    transition: transform 0.3s ease-in-out;
    width: 140px;
    text-align: center;
  }
  
  .partner-card:hover {
    transform: scale(1.05);
  }
  
  .partner-logo {
    width: 80px;
    height: 80px;
    object-fit: contain;
    margin-bottom: 0.5rem;
  }
  
  .partner-name {
    font-size: 1rem;
    font-weight: 600;
  }
  