.about-container {
    /* background-color: black; */
    /* color: white; */
    padding: 10px;
    text-align: center;
  }
  
  .about-heading {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .about-section {
    margin-bottom: 30px;
    text-align: left;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .section-title {
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 10px;
    /* color: #fbbf24; Yellow Highlight */
  }
  
  .section-text {
    font-size: 1.2rem;
    line-height: 1.6;
  }
  
  .section-list {
    list-style-type: disc;
    padding-left: 20px;
    font-size: 1.2rem;
  }
  
  .section-list li {
    margin-bottom: 10px;
  }
  